<template>
    <div class="w-50 mx-auto my-5">
        <div v-if="page_loaded === false">
            <p>The data is being fetched...</p>
        </div>

        <div v-else>
            <h4 class="text-center">Describe your business to potential customers.</h4>

            <alert-component
                class="my-4"
                v-if="hasError"
                v-bind:error="error"></alert-component>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body">
                    <div class="row">
                        <div class="col-sm-2 text-center">
                            <i class="fas fa-info-circle mt-2" style="font-size: 48px;"></i>
                        </div>

                        <div class="col">
                            <p class="font-weight-bold">Your business description will accompany every bid you send.</p>
                            <p class="card-text text-muted">A great, error-free description inspires confidence.</p>
                        </div>
                    </div>
                </div>
            </div>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body">
                    <form v-on:submit.prevent="updateBusinessDescription">
                        <label class="font-weight-bold" for="business-description">Business Description:</label>

                        <div class="mb-3">
                            <textarea id="business-description" class="form-control" placeholder="Describe business, give examples of your projects and give details about a typical project or customer." v-model="business_description"></textarea>
                            <p class="text-muted d-block mb-0 mt-2"><span class="font-weight-bold" v-text="business_description.length">0</span> characters ({{ min_description_length }} minimum).</p>
                        </div>

                        <button class="btn btn-primary btn-lg btn-block" type="submit" v-bind:disabled="loading">
                            <span v-if="loading">Processing...</span>
                            <span v-else>Continue</span>
                        </button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters
    } from "vuex";

    import HasErrorMixin from "./../../../mixins/HasErrorMixin";

    export default {
        computed: {
            business_id: function () {
                return parseInt(this.$route.query.business_id);
            },

            ...mapGetters('BusinessStore', [
                'businesses'
            ]),
        },

        async created() {
            if (isNaN(this.business_id)) {
                return this.$router.push({ name: 'register-business' });
            }

            try {
                let business = this.businesses(this.business_id);

                if (_.isEmpty(business)) {
                    business = await this.$store.dispatch('BusinessStore/fetchBusiness', this.business_id);
                }

                if (! _.isEmpty(business.description)) {
                    this.business_description = business.description;
                }

                this.page_loaded = true;
            } catch (err) {
                err = err.response || err;

                if (err.status === 404) {
                    this.$router.push({ name: 'register-business', query: { 'business-not-found': 1 } });
                }
            }
        },

        data () {
            return {
                loading: false,
                page_loaded: false,

                business_description: '',
                min_description_length: 120,
            };
        },

        methods: {
            async updateBusinessDescription () {
                this.resetError();
                this.loading = true;

                if (this.business_description.length < this.min_description_length) {
                    this.showError(
                        `The description of the business must be at least ${this.min_description_length} characters.`,
                        'danger'
                    );
                } else {
                    const data = {
                        business_id: this.business_id,
                        data: {
                            description: this.business_description
                        }
                    };

                    try {
                        await this.$store.dispatch('BusinessStore/updateBusiness', data);

                        this.$emit('business-description-updated');
                    } catch (err) {
                        this.showError(
                            this.getFirstError(err.data),
                            'danger'
                        );
                    }
                }

                this.loading = false;
            }
        },

        mixins: [
            HasErrorMixin
        ],
    };
</script>



