<template>
    <div v-bind:class="errorClass" role="alert">
        <p class="card-text" v-html="error.message"></p>
    </div>
</template>

<script>
    export default {
        computed: {
            errorClass: function() {
                return "alert alert-" + this.error.type;
            }
        },

        props: {
            error: {
                default: function () {
                    return {message: '', type: 'success'};
                },
                type: Object,
                validator: function (value) {
                    return ['danger', 'success', 'warning'].indexOf(value.type) !== -1;
                }
            },
        }
    };
</script>



