<template>
    <div>
        <header-component></header-component>

        <content-component>
            <div class="container">
                <div v-if="page_loaded === false">
                    <p>The data is being fetched...</p>
                </div>

                <div class="row my-4" v-else>
                    <nav id="dashboard-sidebar" class="col-md-3 py-3 bg-dark shadow-sm">
                        <!--  -->
                    </nav>

                    <main role="main" class="col-md ml-4">
                        <div class="row p-4 bg-white shadow-sm">
                            <div class="col">
                                <div class="row">
                                    <div class="col">
                                        <div>
                                            <img class="business-avatar img-fluid rounded d-block" src="./../../../images/avatar.png" v-bind:alt="business.name">
                                        </div>
                                    </div>

                                    <div class="col-md-9">
                                        <div class="text-left">
                                            <h4 class="font-weight-bold" v-text="business.name"></h4>

                                            <div class="mt-4">
                                                <div class="my-2">
                                                    <i class="fas fa-link"></i>
                                                    <a class="text-muted ml-2" target="_blank" v-bind:href="business.website" v-text="business.website"></a>
                                                </div>

                                                <div class="my-2">
                                                    <i class="fas fa-map-marker-alt"></i>
                                                    <span class="text-muted ml-2">Lorem ipsum dolor sit amet consectetur adipisicing elit.</span>
                                                </div>

                                                <div class="my-2">
                                                    <i class="fas fa-mobile-alt"></i>
                                                    <span class="text-muted ml-2">(541) 754-3010</span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>

                        <div class="row my-3 p-4 bg-white shadow-sm">
                            <div class="col">
                                <h5 class="font-weight-bold">Description</h5>

                                <p v-text="business.description"></p>
                            </div>
                        </div>
                    </main>
                </div>
            </div>
        </content-component>

        <businesses-sidebar-component v-if="display_businesses_sidebar"></businesses-sidebar-component>
    </div>
</template>

<script>
    import {
        mapGetters
    } from "vuex";

    import BusinessesSidebarComponent from "./../modules/BusinessesSidebarComponent.vue";
    import ContentComponent from "./../modules/ContentComponent.vue";
    import HeaderComponent from "./../modules/HeaderComponent.vue";

    export default {
        components: {
            BusinessesSidebarComponent,
            ContentComponent,
            HeaderComponent,
        },

        computed: {
            business_id: function () {
                return parseInt(this.$route.params.id);
            },

            business: function () {
                return this.businesses(this.business_id);
            },

            ...mapGetters('BusinessStore', [
                'businesses'
            ]),

            ...mapGetters('OptionStore', [
                'display_businesses_sidebar'
            ]),
        },

        async created() {
            if (isNaN(this.business_id)) {
                return this.$router.push({ name: 'home', query: { 'business-not-found': 1 } });
            }

            try {
                const business = this.businesses(this.business_id);

                if (_.isEmpty(business)) {
                    await this.$store.dispatch('BusinessStore/fetchBusiness', this.business_id);
                }

                this.page_loaded = true;
            } catch (err) {
                err = err.response || err;

                if (err.status === 404) {
                    this.$router.push({ name: 'home', query: { 'business-not-found': 1 } });
                }
            }
        },

        data () {
            return {
                page_loaded: false
            };
        }
    };
</script>

<style lang="scss" scoped>
    .business-avatar {
        height: 180px;
        width: 180px;
    }
</style>



