<template>
    <div class="w-50 mx-auto my-5">
        <h4 class="text-center">Describe your business to potential customers.</h4>

        <alert-component
            class="my-4"
            v-if="hasError"
            v-bind:error="error"></alert-component>

        <div class="card border-0 shadow-sm my-4">
            <div class="card-body">
                <form v-on:submit.prevent="registerBusiness">
                    <label class="font-weight-bold" for="business-type">Business type:</label>

                    <div class="mb-3">
                        <select class="custom-select d-block w-100" id="business-type" v-model="business_type">
                            <option value="">Choose...</option>
                            <option value="solo-trader">Solo Trader</option>
                            <option value="limited-company">Limited Company</option>
                            <option value="partnership">Partnership</option>
                            <option value="limited-liability-partnership">Limited Liability Partnership</option>
                        </select>
                    </div>

                    <div class="mb-3" v-if="enableCompanyRegistrationNumber">
                        <label class="font-weight-bold" for="registration-number">Company registration number:</label>

                        <div>
                            <input type="text" id="registration-number" class="form-control" placeholder="" v-model="business_registration_number">
                        </div>

                        <p class="text-muted d-block mb-0 mt-2">Look up your number on <span class="font-weight-bold">Companies House</span>.</p>
                    </div>

                    <label class="font-weight-bold" for="business-name">Business name:</label>

                    <div class="mb-3">
                        <input type="text" id="business-name" class="form-control" placeholder="Da Manager LTD." v-model="business_name">
                    </div>

                    <label class="font-weight-bold" for="business-address">Business address:</label>

                    <div class="mb-3">
                        <input type="text" id="business-address" class="form-control" placeholder="Zipcode" v-model="business_zipcode">
                    </div>

                    <button class="btn btn-primary btn-lg btn-block" type="submit" v-bind:disabled="loading">
                        <span v-if="loading">Processing...</span>
                        <span v-else>Continue</span>
                    </button>
                </form>
            </div>
        </div>
    </div>
</template>

<script>
    import HasErrorMixin from "./../../../mixins/HasErrorMixin";

    export default {
        computed: {
            enableCompanyRegistrationNumber() {
                return this.business_type === 'limited-company' || this.business_type === 'limited-liability-partnership';
            },
        },

        data () {
            return {
                loading: false,

                business_registration_number: '',
                business_name: '',
                business_type: '',
                business_zipcode: '',
            };
        },

        methods: {
            async registerBusiness() {
                this.resetError();
                this.loading = true;

                const data = {
                    name: this.business_name,
                    type: this.business_type,
                    zipcode: this.business_zipcode,
                };

                if (this.business_registration_number.length > 0) {
                    data.registration_number = this.business_registration_number;
                }

                try {
                    const business = await this.$store.dispatch('BusinessStore/registerBusiness', data);

                    this.$emit('business-registered', business);
                } catch (err) {
                    this.showError(
                        this.getFirstError(err.data),
                        'danger'
                    );
                }

                this.loading = false;
            }
        },

        mixins: [
            HasErrorMixin
        ]
    };
</script>



