<template>
    <div class="w-50 mx-auto my-5">
        <div v-if="page_loaded === false">
            <p>The data is being fetched...</p>
        </div>

        <div v-else>
            <h4 class="text-center">Profile Enhancement Kick-off.</h4>

            <alert-component
                class="my-4"
                v-if="hasError"
                v-bind:error="error"></alert-component>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body">
                    <form v-on:submit.prevent="updateBusinessProfile">
                        <label class="font-weight-bold" for="founding-year">Year of founding company:</label>

                        <div class="mb-3">
                            <input type="number" id="founding-year" class="form-control" v-model="founding_year">
                        </div>

                        <label class="font-weight-bold" for="company-website">Company Website (optional):</label>

                        <div class="mb-3">
                            <input type="url" id="company-website" class="form-control" placeholder="https://www.example.com" v-model="website">
                        </div>

                        <div
                            v-for="(data, index) in questions"
                            v-bind:key="index"
                            v-bind:index="index">
                            <label class="font-weight-bold" v-bind:for="'question-' + index">{{ data.question }} <span v-if="data.optional">(Optional)</span></label>

                            <div class="mb-3">
                                <textarea v-bind:id="'question-' + index" class="form-control" v-model="data.answer"></textarea>
                                <p class="text-muted d-block mb-0 mt-2"><span class="font-weight-bold" v-text="data.answer.length"></span> characters ({{ min_answer_length }} minimum).</p>
                            </div>
                        </div>

                        <button class="btn btn-primary btn-lg btn-block" type="submit" v-bind:disabled="loading">
                            <span v-if="loading">Processing...</span>
                            <span v-else>Continue</span>
                        </button>
                    </form>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters
    } from "vuex";

    import HasErrorMixin from "./../../../mixins/HasErrorMixin";

    export default {
        computed: {
            business_id: function () {
                return parseInt(this.$route.query.business_id);
            },

            ...mapGetters('BusinessStore', [
                'businesses'
            ]),

            ...mapGetters('OptionStore', [
                'options',
                'option_exists',
            ]),
        },

        async created() {
            if (isNaN(this.business_id)) {
                return this.$router.push({ name: 'register-business' });
            }

            try {
                let business = this.businesses(this.business_id);

                if (_.isEmpty(business)) {
                    business = await this.$store.dispatch('BusinessStore/fetchBusiness', this.business_id);
                }

                if (! _.isEmpty(business.founding_year)) {
                    this.founding_year = business.founding_year;
                }

                if (! _.isEmpty(business.website)) {
                    this.website = business.website;
                }

                const option = {
                    key: 'business_questions',
                    owner: {
                        id: this.business_id,
                        type: 'business'
                    }
                };

                let options = this.options(option);

                if (options.length < 1) {
                    await this.$store.dispatch('OptionStore/fetchBusinessOptions', this.business_id);
                    options = this.options(option);
                }

                if (options.length > 0) {
                    const questions = options[0].value;

                    questions.forEach(data => {
                        const index = this.questions.findIndex(_data => _data.question === data.question);

                        this.questions[index].answer = data.answer;
                    });
                }

                this.page_loaded = true;
            } catch (err) {
                err = err.response || err;

                if (err.status === 404) {
                    this.$router.push({ name: 'register-business', query: { 'business-not-found': 1 } });
                }
            }
        },

        data () {
            return {
                loading: false,
                page_loaded: false,

                founding_year: 1990,
                website: '',
                questions: [
                    {
                        question: 'What differentiates your company from others offering the same service?',
                        answer: '',
                        optional: false,
                    },
                    {
                        question: 'What does offering great service mean to you?',
                        answer: '',
                        optional: true,
                    },
                    {
                        question: 'If a customer likes your bid, what are the typical next steps to get hired and complete the work?',
                        answer: '',
                        optional: false,
                    },
                    {
                        question: 'Under what circumstances would you offer the discounts?',
                        answer: '',
                        optional: true,
                    },
                ],

                min_answer_length: 120,
            };
        },

        methods: {
            async updateBusinessProfile () {
                this.resetError();
                this.loading = true;

                const unfilled_important_questions = this.questions.filter(data => (data.optional === false && data.answer.length < 1) || data.answer.length < this.min_answer_length);

                if (unfilled_important_questions.length > 0) {
                    this.loading = false;

                    if (unfilled_important_questions[0].optional === false && unfilled_important_questions[0].answer.length < 1) {
                        return this.showError(
                            `Please fill: <span class="font-weight-bold">${unfilled_important_questions[0].question}</span>`,
                            'danger'
                        );
                    } else {
                        return this.showError(
                            `Please write at least ${this.min_answer_length} characters for: <span class="font-weight-bold">${unfilled_important_questions[0].question}</span>`,
                            'danger'
                        );
                    }
                }

                const business_questions = this.questions.map(data => {
                    return { question: data.question, answer: data.answer };
                });

                const data = {
                    business_id: this.business_id,
                    data: {
                        founding_year: this.founding_year,
                        website: this.website,
                    }
                };

                const option = {
                    key: 'business_questions',
                    value: business_questions,
                    owner: {
                        id: this.business_id,
                        type: 'business'
                    }
                };

                try {
                    await this.$store.dispatch('BusinessStore/updateBusiness', data);
                    await this.$store.dispatch('OptionStore/registerBusinessOption', option);

                    this.$emit('business-profile-updated');
                } catch (err) {
                    this.showError(
                        this.getFirstError(err.data),
                        'danger'
                    );
                }

                this.loading = false;
            }
        },

        mixins: [
            HasErrorMixin
        ]
    };
</script>



