<template>
    <div class="w-50 mx-auto my-5">
        <div v-if="authUser === undefined || page_loaded === false">
            <p>The data is being fetched...</p>
        </div>

        <div v-else>
            <h4 class="text-center">We're checking for matching job leads.</h4>
            <p class="text-center">In the meantime, there are two things you need to do:</p>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body text-center">
                    <div>
                        <i class="fas fa-info-circle mt-2" style="font-size: 48px;"></i>
                        <p class="font-weight-bold">Confirm your email</p>
                    </div>

                    <p>To ensure you receive all customer requests; follow the instruction that we sent to: <span class="font-weight-bold" v-text="authUser.email"></span></p>
                </div>
            </div>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body text-center">
                    <div>
                        <i class="fas fa-info-circle mt-2" style="font-size: 48px;"></i>
                        <p class="font-weight-bold">Complete the profile of your business.</p>
                    </div>

                    <p>You need a complete profile to respond to your customer requests. You need a great profile to win business. Invest the time in a great profile.</p>
                </div>
            </div>

            <button class="btn btn-primary btn-lg btn-block mt-3" type="submit" @click="updateBusinessAccountConfirmation">
                <span>Continue</span>
            </button>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters
    } from "vuex";

    export default {
        computed: {
            business_id: function () {
                return parseInt(this.$route.query.business_id);
            },

            ...mapGetters('AuthStore', [
                'authUser'
            ]),

            ...mapGetters('BusinessStore', [
                'businesses'
            ]),
        },

        async created() {
            if (isNaN(this.business_id)) {
                return this.$router.push({ name: 'register-business' });
            }

            try {
                let business = this.businesses(this.business_id);

                if (_.isEmpty(business)) {
                    business = await this.$store.dispatch('BusinessStore/fetchBusiness', this.business_id);
                }

                this.page_loaded = true;
            } catch (err) {
                err = err.response || err;

                if (err.status === 404) {
                    this.$router.push({ name: 'register-business', query: { 'business-not-found': 1 } });
                }
            }
        },

        data: function () {
            return {
                page_loaded: false
            };
        },

        methods: {
            updateBusinessAccountConfirmation() {
                this.$emit('business-account-confirmation-updated');
            }
        }
    };
</script>



