<template>
    <div id="page">
        <router-view></router-view>
    </div>
</template>

<script>
    export default {
        created() {
            this.$store.dispatch('OptionStore/fetchAppOptions');

            // Pull the information of the user from the server if they are
            // authenticated.
            const is_authenticated = this.$store.getters['AuthStore/is_authenticated'];

            if (is_authenticated) {
                this.$store.dispatch('AuthStore/authUser').then(user => {
                    this.$store.dispatch('AuthStore/setAuthUser', user);
                });
            }
        }
    };
</script>



