<template>
    <div class="w-50 mx-auto my-5">
        <div v-if="page_loaded === false">
            <p>The data is being fetched...</p>
        </div>

        <div v-else>
            <h4 class="text-center">Where do you work with your customers?</h4>

            <alert-component
                class="my-4"
                v-if="hasError"
                v-bind:error="error"></alert-component>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body">
                    <div class="row">
                        <div class="col-sm-2 text-center">
                            <i class="fas fa-info-circle mt-2" style="font-size: 48px;"></i>
                        </div>

                        <div class="col">
                            <p class="font-weight-bold">You'll see the customers location on every request.</p>
                            <p class="card-text text-muted">You can change these later from your account.</p>
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <ul class="list-group mb-3">
                    <li class="list-group-item d-flex justify-content-between align-items-center"
                        v-for="(localization, index) in localizations"
                        v-bind:key="index"
                        v-bind:index="index"
                        v-bind:class="{'active': localizationExists(localization) !== -1}"
                        @click="toggleLocalization(localization)">
                        {{ localization }}
                        <span class="badge bg-white text-dark" v-if="localizationExists(localization) !== -1">
                            <i class="fas fa-check"></i>
                        </span>
                    </li>
                </ul>

                <button class="btn btn-primary btn-lg btn-block" type="submit" v-bind:disabled="loading" @click="updateBusinessLocalization">
                    <span v-if="loading">Processing...</span>
                    <span v-else>Continue</span>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters
    } from "vuex";

    import HasErrorMixin from "./../../../mixins/HasErrorMixin";

    export default {
        computed: {
            business_id: function () {
                return parseInt(this.$route.query.business_id);
            },

            localizations() {
                const option = {
                    'key': this.option_key,
                    'owner': 'app'
                };

                const localizations = this.options(option);

                return localizations.length > 0 ? localizations[0].value : [];
            },

            business_localizations() {
                const option = {
                    key: this.option_key,
                    owner: {
                        id: this.business_id,
                        type: 'business'
                    }
                };

                const localizations = this.options(option);

                return localizations.length > 0 ? localizations[0].value : [];
            },

            ...mapGetters('OptionStore', [
                'options',
                'option_exists',
            ]),
        },

        async created() {
            if (isNaN(this.business_id)) {
                return this.$router.push({ name: 'register-business' });
            }

            try {
                if (this.business_localizations.length < 1) {
                    await this.$store.dispatch('OptionStore/fetchBusinessOptions', this.business_id);
                }

                if (this.localizations.length < 1) {
                    await this.$store.dispatch('OptionStore/fetchAppOptions');
                }

                this.selected_localizations = this.business_localizations;
                this.page_loaded = true;
            } catch (err) {
                err = err.response || err;

                if (err.status === 404) {
                    this.$router.push({ name: 'register-business', query: { 'business-not-found': 1 } });
                }
            }
        },

        data () {
            return {
                loading: false,
                option_key: 'business_localization',
                page_loaded: false,

                selected_localizations: [],
            };
        },

        methods: {
            localizationExists(localization) {
                return this.selected_localizations.indexOf(localization);
            },

            toggleLocalization(localization) {
                const index = this.localizationExists(localization);

                if (index !== -1) {
                    this.selected_localizations.splice(index, 1);
                } else {
                    this.selected_localizations.push(localization);
                }
            },

            async updateBusinessLocalization() {
                this.resetError();
                this.loading = true;

                const option  = {
                    key: 'business_localization',
                    value: this.selected_localizations,
                    owner: {
                        id: this.business_id,
                        type: 'business'
                    }
                };

                try {
                    if (this.selected_localizations.length > 0) {
                        await this.$store.dispatch('OptionStore/registerBusinessOption', option);
                    }

                    this.$emit('business-localization-updated');
                } catch (err) {
                    this.showError(
                        this.getFirstError(err.data),
                        'danger'
                    );
                }

                this.loading = false;
            }
        },

        mixins: [
            HasErrorMixin
        ],
    };
</script>



