<template>
    <div class="w-50 mx-auto my-5">
        <div v-if="page_loaded === false">
            <p>The data is being fetched...</p>
        </div>

        <div v-else>
            <h4 class="text-center">Which of these related services do you provide?</h4>

            <alert-component
                class="my-4"
                v-if="hasError"
                v-bind:error="error"></alert-component>

            <div class="card border-0 shadow-sm my-4">
                <div class="card-body">
                    <div class="row">
                        <div class="col-sm-2 text-center">
                            <i class="fas fa-info-circle mt-2" style="font-size: 48px;"></i>
                        </div>

                        <div class="col">
                            <p class="font-weight-bold">We will send you requests for the services you select.</p>
                            <p class="card-text text-muted">You can change these later from your account.</p>
                        </div>
                    </div>
                </div>
            </div>

            <div>
                <ul class="list-group mb-3">
                    <li class="list-group-item d-flex justify-content-between align-items-center"
                        v-for="(service, index) in services"
                        v-bind:key="service.id"
                        v-bind:index="index"
                        v-bind:class="{'active': business_service_exists({ business_id, service }) !== -1}"
                        @click="toggleService(service)">
                        {{ service.name }}
                        <span class="badge bg-white text-dark" v-if="business_service_exists({ business_id, service }) !== -1">
                            <i class="fas fa-check"></i>
                        </span>
                    </li>
                </ul>

                <button class="btn btn-primary btn-lg btn-block" type="submit" @click="loadServices" v-if="showLoadMoreServicesButton">
                    <span>Load more services</span>
                </button>

                <button class="btn btn-primary btn-lg btn-block" type="submit" @click="updateBusinessServices">
                    <span>Continue</span>
                </button>
            </div>
        </div>
    </div>
</template>

<script>
    import {
        mapGetters
    } from "vuex";

    import HasErrorMixin from "./../../../mixins/HasErrorMixin";

    export default {
        computed: {
            business_id: function () {
                return parseInt(this.$route.query.business_id);
            },

            showLoadMoreServicesButton() {
                if (this.services_meta.hasOwnProperty('current_page') && this.services_meta.hasOwnProperty('last_page')) {
                    if (this.services_meta.current_page >= this.services_meta.last_page) {
                        return false;
                    }
                }

                return true;
            },

            ...mapGetters('ServiceStore', [
                'business_services',
                'business_service_exists',
                'services',
                'services_meta',
            ]),
        },

        async created() {
            if (isNaN(this.business_id)) {
                return this.$router.push({ name: 'register-business' });
            }

            try {
                if (this.business_services(this.business_id).length < 1) {
                    await this.$store.dispatch('ServiceStore/fetchBusinessServices', this.business_id);
                }

                if (this.services.length < 1) {
                    await this.$store.dispatch('ServiceStore/fetchServices');
                }

                this.page_loaded = true;
            } catch (err) {
                err = err.response || err;

                if (err.status === 404) {
                    this.$router.push({ name: 'register-business', query: { 'business-not-found': 1 } });
                }
            }
        },

        data () {
            return {
                loading: false,
                page_loaded: false,
            };
        },

        methods: {
            loadServices() {
                this.$store.dispatch('ServiceStore/fetchServices');
            },

            async toggleService(service) {
                const data = {
                    business_id: this.business_id,
                    service
                };

                service = {
                    owner: {
                        id: this.business_id,
                    },

                    ...service
                };

                try {
                    if (this.business_service_exists(data) !== -1) {
                        await this.$store.dispatch('ServiceStore/unregisterBusinessService', service);
                    } else {
                        await this.$store.dispatch('ServiceStore/registerBusinessService', service);
                    }
                } catch (err) {
                    console.log(err);
                }
            },

            updateBusinessServices() {
                this.$emit('business-services-updated');
            }
        },

        mixins: [
            HasErrorMixin
        ]
    };
</script>



