<template>
    <div>
        <header-component></header-component>

        <content-component>
            <div class="container">
                <div class="w-50 mx-auto my-5">
                    <h4 class="text-center">Create your account<br>and start winning your business today.</h4>

                    <alert-component
                        class="my-4"
                        v-if="hasError"
                        v-bind:error="error"></alert-component>

                    <div class="card border-0 shadow-sm my-4">
                        <div class="card-body">
                            <form v-on:submit.prevent="registerUser">
                                <label class="font-weight-bold" for="first-name">Enter your contact information:</label>

                                <div class="row">
                                    <div class="col-md-6 mb-3">
                                        <input type="text" class="form-control" id="first-name" placeholder="John" v-model="first_name">
                                    </div>

                                    <div class="col-md-6 mb-3">
                                        <input type="text" class="form-control" placeholder="Smith" v-model="last_name">
                                    </div>
                                </div>

                                <div class="mb-3">
                                    <input type="email" class="form-control" placeholder="you@example.com" v-model="email">
                                </div>

                                <div class="mb-3">
                                    <div class="input-group">
                                        <div class="input-group-prepend">
                                            <span class="input-group-text">+44</span>
                                        </div>

                                        <input type="text" class="form-control" placeholder="Phone number" v-model="phone">
                                    </div>
                                </div>

                                <label class="font-weight-bold" for="password">Secure your account with a password:</label>

                                <div class="mb-3">
                                    <input type="password" id="password" class="form-control" placeholder="password" v-model="password">
                                </div>

                                <div class="custom-control custom-checkbox mb-3">
                                    <input class="custom-control-input" id="terms" type="checkbox" v-model="terms_accepted">
                                    <label class="custom-control-label" for="terms">I agree to {{ $appName }} <a href="#">terms of service</a> and <a href="#">privacy policy</a>.</label>
                                </div>

                                <button class="btn btn-primary btn-lg btn-block" type="submit" v-bind:disabled="loading">
                                    <span v-if="loading">Processing...</span>
                                    <span v-else>Sign up</span>
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </content-component>
    </div>
</template>

<script>
    import {
        USERS
    } from './../../routes/api';

    import HasErrorMixin from "./../../mixins/HasErrorMixin";
    import ContentComponent from "./../modules/ContentComponent.vue";
    import HeaderComponent from "./../modules/HeaderComponent.vue";

    export default {
        components: {
            ContentComponent,
            HeaderComponent,
        },

        data() {
            return {
                loading: false,
                first_name: '',
                last_name: '',
                email: '',
                password: '',
                phone: '',
                terms_accepted: false,
            };
        },

        methods: {
            async registerUser() {
                this.resetError();
                this.loading = true;

                if (! this.terms_accepted) {
                    this.showError(
                        'Sorry! Please accept our terms of service and privacy policy to proceed.',
                        'danger'
                    );

                    this.loading = false;

                    return;
                }

                const data = {
                    first_name: this.first_name,
                    last_name: this.last_name,
                    email: this.email,
                    password: this.password,
                    phone: this.phone,
                };

                try {
                    const resp = await axios.post(USERS, data);
                    const user = resp.data.data;

                    const credentials = {
                        username: data.email,
                        password: data.password,
                    };

                    await this.$store.dispatch('AuthStore/authRequest', credentials);
                    await this.$store.dispatch('AuthStore/setAuthUser', user);

                    const setup_business = this.$route.query.setup_business;

                    let route = { name: 'home' };

                    if (setup_business == 1) {
                        route.name = 'register-business';
                    }

                    this.$router.push( route );
                } catch (err) {
                    this.showError(
                        this.getFirstError(err.data),
                        'danger'
                    );
                }

                this.loading = false;
            }
        },

        mixins: [
            HasErrorMixin
        ]
    };
</script>



