<template>
    <div>
        <header-component></header-component>

        <content-component>
            <div class="container">
                <div class="w-50 mx-auto my-5">
                    <h4>Log in</h4>

                    <alert-component
                        class="my-4"
                        v-if="hasError"
                        v-bind:error="error"></alert-component>

                    <div class="card border-0 shadow-sm my-4">
                        <div class="card-body">
                            <form v-on:submit.prevent="requestLogin">
                                <div class="mb-3">
                                    <input type="email" class="form-control" placeholder="you@example.com" v-model="username">
                                </div>

                                <div class="mb-3">
                                    <input type="password" id="password" class="form-control" placeholder="password" v-model="password">
                                </div>

                                <button class="btn btn-primary btn-lg btn-block" type="submit" v-bind:disabled="loading">
                                    <span v-if="loading">Processing...</span>
                                    <span v-else>Sign in</span>
                                </button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </content-component>
    </div>
</template>

<script>
    import HasErrorMixin from "./../../mixins/HasErrorMixin";

    import ContentComponent from "./../modules/ContentComponent.vue";
    import HeaderComponent from "./../modules/HeaderComponent.vue";

    export default {
        components: {
            ContentComponent,
            HeaderComponent,
        },

        data() {
            return {
                loading: false,
                username: '',
                password: '',
            };
        },

        methods: {
            async requestLogin() {
                this.resetError();
                this.loading = true;

                const props = {
                    username: this.username,
                    password: this.password
                };

                try {
                    await this.$store.dispatch('AuthStore/authRequest', props);

                    const user = await this.$store.dispatch('AuthStore/authUser');

                    await this.$store.dispatch('AuthStore/setAuthUser', user);

                    const path = this.$route.query.redirect;

                    if (path !== undefined) {
                        this.$router.push({ path });
                    } else {
                        this.$router.push({ name: 'home' });
                    }
                } catch (err) {
                    this.showError(
                        this.getFirstError(err.data),
                        'danger'
                    );
                }

                this.loading = false;
            }
        },

        mixins: [
            HasErrorMixin
        ]
    };
</script>



