<template>
    <header id="header" class="site-header">
        <nav class="navbar navbar-expand-lg navbar-light bg-white shadow-sm">
            <div class="container-fluid">
                <div class="row">
                    <div class="col menu-button" v-if="is_authenticated" @click="showBusinessesSidebar">
                        <i class="fas fa-ellipsis-h"></i>
                    </div>

                    <div class="col">
                        <router-link class="navbar-brand" :to="{name: 'home'}" v-text="$appName"></router-link>
                    </div>
                </div>

                <div v-if="hasCustomData">
                    <slot></slot>
                </div>

                <div v-else>
                    <button type="button" data-toggle="collapse" data-target="#navbar-navigation"
                            aria-controls="navbar-navigation" aria-expanded="false" aria-label="Toggle navigation"
                            class="navbar-toggler" style="">
                        <span class="navbar-toggler-icon"></span>
                    </button>

                    <div id="navbar-navigation" class="collapse navbar-collapse">
                        <ul class="navbar-nav ml-auto" v-if="is_authenticated">
                            <div class="dropdown">
                                <button id="dropdown-profile" class="btn btn-light dropdown-toggle" type="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                    <img src="./../../../images/avatar.png" v-bind:alt="full_name">
                                    <span v-text="full_name"></span>
                                </button>

                                <div class="dropdown-menu" aria-labelledby="dropdown-profile">
                                    <a class="dropdown-item" href="#" @click.prevent="logOut">Log out</a>
                                </div>
                            </div>
                        </ul>

                        <ul class="navbar-nav ml-auto" v-else>
                            <li class="nav-item">
                                <router-link class="nav-link" :to="{name: 'login'}">Sign in</router-link>
                            </li>

                            <li class="nav-item">
                                <router-link class="nav-link" :to="{name: 'register', query: { setup_business: 1 }}">Sign up</router-link>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </nav>
    </header>
</template>

<script>
    import { mapGetters } from 'vuex';

    export default {
        computed: {
            hasCustomData () {
                return !!this.$slots.default;
            },

            full_name() {
                if (this.authUser !== undefined) {
                    return this.authUser.first_name + ' ' + this.authUser.last_name;
                }

                return '';
            },

            ...mapGetters('AuthStore', [
                "authUser",
                "is_authenticated"
            ]),
        },

        methods: {
            logOut() {
                this.$store.dispatch('AuthStore/authLogout').then(
                    () => this.$router.push({ name: 'login' })
                );
            },

            showBusinessesSidebar() {
                this.$store.dispatch('OptionStore/toggleBusinessesSidebar', 'show');
            }
        }
    }
</script>

<style lang="scss" scoped>
    .menu-button {
        align-items: center;
        background-color: #F6F6F6;
        border-radius: 2px;
        // box-shadow: 0 15px 35px rgba(50,50,93,.1),0 5px 15px rgba(0,0,0,.07);
        cursor: pointer;
        display: flex;

        &:hover {
            background-color: #F5F5F5;
        }
    }

    #dropdown-profile img {
        border-radius: 50px;
        height: 36px;
        margin-right: 8px;
        width: 36px;
    }
</style>



